.home-container {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.shader-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Places iframe behind the h1 */
}

h1 {
  color: white; /* Ensures text is visible against the dynamic gradient */
  text-align: center;
  z-index: 1; /* Ensures h1 is above the iframe */
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Optional: improves text readability */
}